import classnames from 'classnames';
import styles from './steps.module.scss';
import { steps } from '../infos/infos';
import house from './../../assets/animations/house.lottie';
import size from './../../assets/animations/size.lottie';
import bulb from './../../assets/animations/bulb.lottie';
import arrow from './../../assets/animations/arrow-down.lottie';
import { DotLottiePlayer } from '@dotlottie/react-player';
import Icon from '../../elements/icon/Icon';
import { HashLink } from 'react-router-hash-link';
import React from 'react';
import Title from '../../elements/title/Title';

const Steps = React.memo(() => {
  return (
    <div id='steps' className={styles.stepsWrapper}>
      <div className={classnames(styles.container, styles.steps)}>
        <Title variant='big' text='ВИВІСКА - ЦЕ ПРОСТО!' />
        <Title variant='subtitleSmall' text='ЩО ПОТРІБНО ДЛЯ ЗАМОВЛЕННЯ?' />
        <div className={styles.content}>
          <div className={styles.cardContainer}>
            <div className={styles.titleContainer}>
              <div className={styles.animationContainer}>
                <DotLottiePlayer src={house} autoplay loop />
              </div>
              <h4 className={styles.cardTitle}>{steps[0].title}</h4>
            </div>

            <p className={styles.text}>{steps[0].text}</p>
          </div>

          <div className={styles.cardContainer}>
            <div className={styles.titleContainer}>
              <div className={styles.animationContainer}>
                <DotLottiePlayer src={size} autoplay loop />
              </div>
              <h4 className={styles.cardTitle}>{steps[1].title}</h4>
            </div>

            <p className={styles.text}>{steps[1].text}</p>
          </div>
          <div className={styles.cardContainer}>
            <div className={styles.titleContainer}>
              <div className={styles.animationContainerIdea}>
                <DotLottiePlayer src={bulb} autoplay loop />
              </div>
              <h4 className={styles.cardTitle}>{steps[2].title}</h4>
            </div>

            <p className={styles.text}>{steps[2].text}</p>
          </div>
        </div>
        <HashLink smooth to='#titleSecond' className={styles.arrowDown}>
          <DotLottiePlayer src={arrow} autoplay loop />
        </HashLink>
        <Title id='titleSecond' variant='middle' text="ЗВ'ЯЖІТЬСЯ З НАМИ" />
        <div className={styles.buttons}>
          <div className={styles.buttonContainer}>
            <h4 className={styles.subtitleSecond}>
              По телефону чи через месенджери:
            </h4>
            <div className={classnames(styles.link, styles.linkSecond)}>
              <span className={styles.tel}>(066) 46-799-95</span>
            </div>
            <div className={classnames(styles.link, styles.linkSecond)}>
              <span className={styles.tel}>(068) 28-102-14</span>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
});

export default Steps;
